<template>
  <div class="order_refund">
    <div class="left_menu">
      <div>
        <el-select
          @change="changeChannel"
          v-model="channel"
          placeholder="请选择渠道"
        >
          <el-option label="全部" value="all"></el-option>
          <el-option
            v-for="(item, i) in channels"
            :key="i"
            :label="item"
            :value="item"
          >
          </el-option>
        </el-select>
      </div>
      <div class="subMenu">
        <el-menu
          @select="selectSku"
          default-active="1-1"
          class="el-menu-vertical-demo"
        >
          <el-submenu
            v-for="(item, i) in storeMenu"
            :key="i"
            :index="i + 1 + ''"
          >
            <template slot="title">
              <span>{{ item.country }}</span>
            </template>
            <el-menu-item-group>
              <el-menu-item
                v-for="(item, j) in item.stations"
                :key="j"
                :index="i + 1 + '-' + (j + 1)"
                >{{ item }}</el-menu-item
              >
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </div>
      <button class="toDetail" @click="go_route('/ReturnTimeDetail')">
        详情查看
      </button>
    </div>
    <div class="rightbox">
      <div class="topOption">
        <!-- 今天以前，15天 -->

        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item label="时间范围">
            <el-select
              v-model="formData.daterange"
              placeholder="请选择时间范围"
            >
              <el-option
                v-for="(item, i) in dateranges"
                :key="i"
                :label="item + '天'"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button @click="search" type="primary">搜索</el-button>
          </el-form-item>
          <div>
            <el-form-item label="总订单量">
              <el-input disabled v-model="formData.all_num"></el-input>
            </el-form-item>
            <el-form-item label="退货单量">
              <el-input disabled v-model="formData.refundnum"></el-input>
            </el-form-item>
          </div>
        </el-form>
      </div>
      <div class="chart_box">
        <div
          class="chart"
          ref="chart"
          style="width:80%;height:500px;margin:0 auto;"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      chartDataObj: [],
      data1: [],
      data2: [],
      data3: [],
      chartData: {
        xAxis: [],
        seriesData: []
      },
      storeMenu: [],
      formData: {
        daterange: "",
        all_num: "",
        refundnum: ""
      },
      station: "",
      country: "",
      dateranges: [7, 15, 30, 45, 60, 90, 150],
      channel: "all",
      channels: []
    };
  },
  methods: {
    search() {
      console.log(this.channel, this.station, this.formData.daterange);
      if (
        !this.channel ||
        this.channel === "all" ||
        !this.station ||
        this.station === "all" ||
        !this.country ||
        this.country === "all" ||
        !this.formData.daterange
      ) {
        alert("必须选择渠道、国家、站点、时间范围！");
        return;
      }
      let url = "server/order_management/refund_times_monitor/";
      let formData = new FormData();
      formData.append("channel", this.channel);
      formData.append("station", this.station);
      formData.append("country", this.country);
      formData.append("days", this.formData.daterange);
      this.$axios.post(this._url2 + url, formData).then(res => {
        this.formData.all_num = res.data.all_orders;
        this.formData.refundnum = res.data.refund_orders;
        this.chartData.xAxis = [];
        this.chartData.seriesData = [];
        this.chartDataObj = res.data.data;
        this.chartDataObj.forEach(i => {
          this.chartData.xAxis.push(i.day);
          this.chartData.seriesData.push(i.order_num);
        });
        this.initEchart();
      });
    },
    initEchart() {
      let option = {
        title: {
          text: "订单退款"
        },
        tooltip: {
          trigger: "axis"
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.chartData.xAxis
          // axisLabel: {
          //   interval: 0,
          //   rotate: 60
          // }
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "退货时间",
            type: "line",
            stack: "退货时间",
            data: this.chartData.seriesData,
            label: {
              normal: {
                show: true,
                position: "top"
              }
            }
          }
        ]
      };
      let chart = this.$refs.chart;
      if (chart) {
        this.myChart = this.$echarts.init(chart);
        this.myChart.setOption(option);
        window.addEventListener("resize", () => {
          this.myChart.resize();
        });
      }
      this.$on("hook:destoryed", () => {
        window.removeEventListener("resize", () => {
          this.myChart.resize();
        });
      });
    },
    changeDate(val) {
      console.log(val);
    },
    go_route(val) {
      console.log(val);
      this.$router.push(val);
    },
    changeChannel() {
      this.storeMenu = [];
      this.formData.all_num = "";
      this.formData.refundnum = "";
      if (this.channel !== "all") {
        let idx = this.channels.findIndex(i => i === this.channel);
        let countryData = this.data2[idx];
        countryData.forEach((item, index) => {
          let obj = {};
          obj["country"] = item;
          console.log(index, this.data3[idx][index]);
          obj["stations"] = this.data3[idx][index];
          this.storeMenu.push(obj);
        });
      }
    },
    selectSku(key, keyPath) {
      this.formData.all_num = "";
      this.formData.refundnum = "";
      let [stationIdx, countryIdx] = key.split("-").map(i => {
        return Number(i) - 1;
      });
      let item = this.storeMenu[stationIdx];
      this.country = item.country;
      this.station = item.stations[countryIdx];
      this.search();
    }
  },
  mounted() {
    // 初始化echart
    this.initEchart();
  },
  created() {
    let url = "server/personnel_management/area_sign1/";
    this.$axios.get(this._url2 + url).then(res => {
      console.log(res);
      // 渠道
      this.data1 = res.data.data1;
      // 国家
      this.data2 = res.data.data2;
      // 站点
      this.data3 = res.data.data3;
      this.channels = this.data1 || [];
      this.formData.daterange = 7;
      this.channel = this.channels[0];
      this.changeChannel();
      // console.log(this.storeMenu);

      this.country = this.storeMenu[0].country;
      this.station = this.storeMenu[0].stations[0];
      console.log(this.channel, this.station, this.formData.daterange);
      this.search();
    });
  }
};
</script>

<style scoped>
.order_refund {
  padding: 50px;
}
.order_refund .rightbox {
  margin: 0 auto;
  width: 100%;
}
.order_refund .chart_box {
  padding: 20px;

  margin: 0 50px;
  background: rgba(255, 255, 255, 0.9);
}
.order_refund .topOption {
  display: flex;
  margin-left: 50px;
}
.topOption >>> .el-input {
  width: 150px;
  margin-left: 20px;
}
.order_refund .chart {
  background: #fff;
  margin: 30px auto;
}
.order_refund {
  display: flex;
}
.subMenu {
  background: rgba(255, 255, 255, 0.9);
  margin-top: 30px;
}
.order_refund >>> .el-form-item__label {
  color: #fff;
}
.toDetail {
  margin-top: 30px;
  width: 100%;
  height: 40px;
  background: dodgerblue;
  color: #fff;
  border-radius: 5px;
}
</style>



